<template>
  <div class="wrapper">
    <div class="home">
      <h1>富文本编辑器1 wangEditor</h1>
      <button type="button" class="btn" @click="getEditorData">
        获取传输内容
      </button>
      <div id="demo1"></div>
      <h1>富文本编辑器2 tinyMCE</h1>
      <vue-tinymce v-model="tmContent" :setup="tmSetup" :setting="tmSetting" />
      <h1>markdown编辑器 mavonEditor</h1>
      <mavon-editor v-model="mdValue" style="margin-bottom:30px" />
    </div>
  </div>
</template>

<script>
import wangEditor from "wangeditor";

export default {
  data() {
    return {
      tmContent: "<p>html content</p>",
      tmSetting: {
        height: 600,
        menubar: true,
        toolbar:
          "undo redo | fullscreen | formatselect alignleft aligncenter alignright alignjustify | link unlink | numlist bullist | image media table | fontselect fontsizeselect forecolor backcolor | bold italic underline strikethrough | indent outdent | superscript subscript | removeformat |",
        toolbar_drawer: "sliding",
        quickbars_selection_toolbar:
          "removeformat | bold italic underline strikethrough | fontsizeselect forecolor backcolor",
        plugins: "link image media table lists fullscreen quickbars",
        language: "zh_CN",
        language_url: "/tinymce/langs/zh_CN.js",
        branding: false
      },
      mdValue: "",
      wangEditor: null,
      editorData: ""
    };
  },
  mounted() {
    const weditor = new wangEditor(`#demo1`);
    // 配置 onchange 回调函数，将数据同步到 vue 中
    weditor.config.onchange = newHtml => {
      this.editorData = newHtml;
    };
    // editor.config.height = 300;
    // enable上传图片功能
    weditor.config.uploadImgServer = "/upload-img";
    // 创建编辑器
    weditor.create();
    //绑定编辑器对象到data里面克
    this.wangEditor = weditor;
  },
  methods: {
    tmSetup(editor) {
      console.log(editor);
    },
    getEditorData() {
      // 通过代码获取编辑器内容
      let data = this.wangEditor.txt.html();
      console.log(data);
      console.log(this.mdValue);
    }
  },
  beforeDestroy() {
    // 调用销毁 API 对当前编辑器实例进行销毁
    this.wangEditor.destroy();
    this.wangEditor = null;
  }
};
</script>

<style lang="scss">
body {
  margin: 0;
}
.wrapper {
  background: #f7f7f7;
  overflow: hidden;
  h1 {
    font-weight: 300;
    margin: 0 10;
    background-color: #e7e7e7;
    color: #6a6a6a;
  }
}
.home {
  width: 1200px;
  margin: auto;
  position: relative;
  #demo1 {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px 0px;
    border: none !important;
  }
  .btn {
    padding: 5px 10px;
    cursor: pointer;
  }
  h3 {
    margin: 30px 0 15px;
  }
}
</style>
