import Vue from 'vue'
import App from './App.vue'
import router from './router'
import mavonEditor from 'mavon-editor'
import VueTinymce from "@packy-tang/vue-tinymce"

import 'mavon-editor/dist/css/index.css'

Vue.config.productionTip = false
Vue.use(mavonEditor)
Vue.use(VueTinymce)


new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
